

import Masker from 'vux/src/components/masker/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import Bus from '@/utils/bus'

export default {
title: {
chinese: '智能家电',
english: 'Smart Appliances'
},
components: {
Masker,
Grid,
GridItem
},
data () {
return {
headerTitle: '日用电器',
cardList: [{
title: {
chinese: '客厅空调'
},



img: './static/img/air_conditioner.png',
icon: './static/icon/air_conditioner.png',
buttonIcon: {
switch: './static/icon/switch.png',
windSpeed: './static/icon/wind_speed.png',
up: './static/icon/up.png',
down: './static/icon/down.png'
},
path: '/HelloFromVux'
}]
}
},
mounted () {
Bus.$emit('change-header-title', this.headerTitle)
}
}



